/**
 * Class qun l vic parse v tm kim cc cp comment tags
 * H tr tm kim theo pattern vi wildcard (*)
 * 
 * @example
 * const parser = new TemplateDetectorService(document.body);
 * const pairs = parser.find('one-template:*');
 * parser.display(pairs);
 */
export class TemplateDetectorService {
    /**
     * Khi to parser vi element gc
     * @param {Element} rootElement - Element gc  tm kim (mc nh: document.body)
     */
    constructor(rootElement = document.body) {
        this.rootElement = rootElement;
        this.cachedComments = null;
    }

    /**
     * t element gc mi
     * @param {Element} element - Element gc mi
     */
    setRootElement(element) {
        this.rootElement = element;
        this.cachedComments = null;
        return this;
    }

    /**
     * Ly tt c comment nodes t rootElement
     * @param {boolean} useCache - S dng cache hay khng
     * @returns {Array<Comment>}
     */
    getAllComments(useCache = true) {
        if (useCache && this.cachedComments) {
            return this.cachedComments;
        }

        const comments = [];
        const walker = document.createTreeWalker(
            this.rootElement,
            NodeFilter.SHOW_COMMENT,
            null,
            false
        );

        let node;
        while (node = walker.nextNode()) {
            comments.push(node);
        }

        if (useCache) {
            this.cachedComments = comments;
        }

        return comments;
    }

    /**
     * Parse mt comment node  ly thng tin
     * @param {Comment} commentNode - Comment node cn parse
     * @returns {Object|null} - Thng tin tag hoc null nu khng phi tag hp l
     */
    parseComment(commentNode) {
        const text = commentNode.nodeValue.trim();

        // Pattern tng qut: [prefix:name] hoc [prefix]
        // Kim tra tag m: <!-- [prefix:name attribute="..."] -->
        const openMatch = text.match(/^\[([^\/\]]+?)(?:\s+(.+))?\]$/);
        if (openMatch) {
            const fullName = openMatch[1];
            const attributes = {};

            if (openMatch[2]) {
                // Parse cc attributes nh subscribe="userState,items"
                const attrMatch = openMatch[2].match(/(\w+)="([^"]+)"/g);
                if (attrMatch) {
                    attrMatch.forEach(attr => {
                        const [key, value] = attr.split('=');
                        attributes[key] = value.replace(/"/g, '');
                    });
                }
            }

            return {
                type: 'open',
                fullName: fullName,
                attributes: attributes,
                node: commentNode
            };
        }

        // Kim tra tag ng: <!-- [/prefix:name] -->
        const closeMatch = text.match(/^\[\/([^\/\]]+?)\]$/);
        if (closeMatch) {
            return {
                type: 'close',
                fullName: closeMatch[1],
                node: commentNode
            };
        }

        return null;
    }

    /**
     * Ly tt c cc siblings gia hai node (cng parent)
     * @param {Node} startNode - Node bt u
     * @param {Node} endNode - Node kt thc
     * @returns {Array<Node>|null} - Mng nodes hoc null nu khng cng parent
     */
    getNodesBetween(startNode, endNode) {
        // Kim tra xem c cng parent khng
        if (startNode.parentNode !== endNode.parentNode) {
            return null;
        }

        const nodes = [];
        let current = startNode.nextSibling;

        while (current && current !== endNode) {
            nodes.push(current);
            current = current.nextSibling;
        }

        return nodes;
    }

    /**
     * Chuyn i pattern thnh regex
     * @param {string} pattern - Pattern vi wildcard (*)
     * @returns {RegExp}
     * 
     * @example
     * patternToRegex('*:*')              // Match tt c
     * patternToRegex('one-*')            // Match "one-template", "one-component"
     * patternToRegex('one-template:*')   // Match "one-template:profile", etc.
     */
    patternToRegex(pattern) {
        if (!pattern || pattern === '*' || pattern === '*:*') {
            return /.*/;
        }

        // Escape cc k t c bit trong regex (tr *)
        let regexStr = pattern
            .replace(/[.+?^${}()|[\]\\]/g, '\\$&')  // Escape regex special chars
            .replace(/\*/g, '.*');                    // Convert * thnh .*

        // Thm ^ v $  match chnh xc ton b string
        regexStr = '^' + regexStr + '$';

        return new RegExp(regexStr);
    }

    /**
     * Kim tra xem mt tn c khp vi pattern khng
     * @param {string} fullName - Tn y  ca tag
     * @param {string} pattern - Pattern  so khp
     * @returns {boolean}
     */
    matchPattern(fullName, pattern) {
        const regex = this.patternToRegex(pattern);
        return regex.test(fullName);
    }

    /**
     * Tm tt c cc cp tags theo pattern
     * @param {string} pattern - Pattern  lc (mc nh: '*:*')
     * @param {Object} options - Ty chn b sung
     * @param {boolean} options.useCache - S dng cache comments
     * @returns {Array<Object>} - Mng cc cp tags tm c
     * 
     * @example
     * parser.find('*:*')                   // Tm tt c
     * parser.find('one-template:*')        // Ch tm one-template
     * parser.find('one-template:profile')  // Tm chnh xc profile
     */
    find(pattern = '*:*', options = {}) {
        const { useCache = true } = options;

        const comments = this.getAllComments(useCache);
        const parsed = comments.map(c => this.parseComment(c)).filter(p => p !== null);

        const pairs = [];
        const stack = [];

        for (let i = 0; i < parsed.length; i++) {
            const current = parsed[i];

            // Kim tra xem c khp vi pattern khng
            if (!this.matchPattern(current.fullName, pattern)) {
                continue;
            }

            if (current.type === 'open') {
                // y tag m vo stack
                stack.push({
                    ...current,
                    index: i
                });
            } else if (current.type === 'close') {
                // Tm tag m tng ng t stack
                let foundIndex = -1;
                for (let j = stack.length - 1; j >= 0; j--) {
                    if (stack[j].fullName === current.fullName) {
                        foundIndex = j;
                        break;
                    }
                }

                if (foundIndex !== -1) {
                    const openTag = stack[foundIndex];

                    // Ly cc nodes nm gia
                    const nodesBetween = this.getNodesBetween(openTag.node, current.node);

                    // Ch thm vo nu cng parent
                    if (nodesBetween !== null) {
                        pairs.push({
                            fullName: current.fullName,
                            openTag: openTag.node,
                            closeTag: current.node,
                            attributes: openTag.attributes,
                            nodes: nodesBetween,
                            parent: openTag.node.parentNode
                        });
                    }

                    // Xa khi stack
                    stack.splice(foundIndex, 1);
                }
            }
        }

        return pairs;
    }

    /**
     * Tm mt cp tag duy nht theo pattern
     * @param {string} pattern - Pattern  tm
     * @returns {Object|null} - Cp tag tm c hoc null
     */
    findOne(pattern) {
        const pairs = this.find(pattern);
        return pairs.length > 0 ? pairs[0] : null;
    }

    /**
     * Lc cc cp tags theo iu kin ty chnh
     * @param {Function} filterFn - Hm filter (pair) => boolean
     * @param {string} pattern - Pattern ban u
     * @returns {Array<Object>}
     */
    filter(filterFn, pattern = '*:*') {
        const pairs = this.find(pattern);
        return pairs.filter(filterFn);
    }

    /**
     * m s lng cp tags theo pattern
     * @param {string} pattern - Pattern  m
     * @returns {number}
     */
    count(pattern = '*:*') {
        return this.find(pattern).length;
    }

    /**
     * Ly danh sch tt c cc tn tags duy nht
     * @param {string} pattern - Pattern  lc
     * @returns {Array<string>}
     */
    getUniqueNames(pattern = '*:*') {
        const pairs = this.find(pattern);
        return [...new Set(pairs.map(p => p.fullName))];
    }

    /**
     * Hin th kt qu tm kim trong console
     * @param {Array<Object>|string} pairsOrPattern - Mng pairs hoc pattern string
     * @param {Object} options - Ty chn hin th
     */
    display(pairsOrPattern, options = {}) {
        const {
            showAttributes = true,
            showParent = true,
            showNodes = true,
            maxNodePreview = 3
        } = options;

        let pairs, patternInfo = '';

        // Nu truyn vo l string, tm kim trc
        if (typeof pairsOrPattern === 'string') {
            patternInfo = ` vi pattern "${pairsOrPattern}"`;
            pairs = this.find(pairsOrPattern);
        } else {
            pairs = pairsOrPattern;
        }

        console.log(`\n${'='.repeat(80)}`);
        console.log(`Tm thy ${pairs.length} cp tags${patternInfo}`);
        console.log(`${'='.repeat(80)}\n`);

        if (pairs.length === 0) {
            console.log(' Khng tm thy cp tags no!');
            return;
        }

        pairs.forEach((pair, index) => {
            console.log(`\n Cp ${index + 1}: [${pair.fullName}]`);
            console.log('-'.repeat(60));

            if (showAttributes) {
                console.log(' Attributes:',
                    Object.keys(pair.attributes).length > 0
                        ? pair.attributes
                        : '(khng c)'
                );
            }

            if (showParent) {
                console.log(' Parent:', pair.parent.tagName.toLowerCase(),
                    pair.parent.className ? `class="${pair.parent.className}"` : ''
                );
            }

            if (showNodes) {
                console.log(` Ni dung (${pair.nodes.length} nodes):`);

                const previewNodes = pair.nodes.slice(0, maxNodePreview);
                previewNodes.forEach(node => {
                    if (node.nodeType === Node.ELEMENT_NODE) {
                        const className = node.className ? ` class="${node.className}"` : '';
                        console.log(`    <${node.tagName.toLowerCase()}${className}>`);
                    } else if (node.nodeType === Node.TEXT_NODE && node.nodeValue.trim()) {
                        const text = node.nodeValue.trim().substring(0, 50);
                        console.log(`    Text: "${text}${text.length >= 50 ? '...' : ''}"`);
                    }
                });

                if (pair.nodes.length > maxNodePreview) {
                    console.log(`    ... v ${pair.nodes.length - maxNodePreview} nodes khc`);
                }
            }
        });

        console.log(`\n${'='.repeat(80)}\n`);
    }

    /**
     * Xa cache  buc parse li
     */
    clearCache() {
        this.cachedComments = null;
        return this;
    }
}